.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

img {
  height: 100px;
  width: 100px;
}

.app__form {
  position: fixed !important;
  bottom: 0 !important;
  z-index: 1 !important;
  background-color: #e9e9eb !important;
  width: 100%;
}
.app__formControl {
  display: flex !important;
  flex-direction: row !important;
}
.app__inputLabel {
  flex: 1;
}
.app__button {
  flex: 0;
}

.app__button1{
  display: flex;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.app__button2{
  display: flex;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}

